export default {
    name:'center',
    //获取登录名
    computed:{
        loginname:function(){
            return this.$store.state.loginname;
        }
    },
    created:function(){
        //跳转到用户界面，使用编程式路由
        //this.$router.push 这个方法会向history添加一个记录 点击后退返回到上一个页面
        //this.$router.replace 这个放啊发不会向history  添加一个记录 ，点击后退
        // this.$router.push('/user/'+this.loginname);
        this.$router.replace('/user/'+this.loginname);

    }
}



